@media screen and (max-width: 1199px) {  
// ______header______
  
    .nav_list {
        gap: 25px;
    }

    .header_btn {
        padding: 17px 17px;
    }

    .header_logo img {
        max-width: 85px;
    }

// ______first screen______

    .first_screen_content {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-left: auto;
        margin-right: auto;
        & p {
            text-align: center;
        }
        & p:first-of-type:before {
            left: 50%;
            transform: translate(-50%, 0);
        }
    }

    .first_screen_title {
        text-align: center;
    }

    .first_screen_btn_container {
        justify-content: center;
    }

    .first_screen_img {
        left: -150px;
    }

    .first_screen_car_img {
        display: none;
    }

// ______use page______

    .use_mobile_container {
        max-width: 40%;
    }

    .use_content_container {
        max-width: 60%;
        & .use_content_text p {
            font-size: 17px;
        }
    }

// ______about us page______

    .about_us_slider {
        & .slide_content {
            max-width: 773px;
        }
        & .arrow_container {
            max-width: 809px;
        }
    }


// ______price page______
    .price_container .price_container_item .price_container_content ul li {
        font-size: 16px;
    }

    ul .ul_second {
        padding-left: 31px;
    }
    .price_container_item .btn-lg {
        padding: 24px 40px 24px 75px;
    }

    .price_container {
        gap: 25px;
    }

// ______contact page______

    .contact_section {
        & .contact_container {
            & .img_box {
                & img {
                    max-width: 120%;
                    margin-left: -20px;
                }
            }
            & .input_container form {
                margin-left: 5px;
            }
        }
    }

}
